import React, { Component } from 'react';

import FormErrors from './FormErrors';

class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      formErrors: { email: "" },
      emailValid: false,
      formValid: false
    };
  }

  handleUserInput = e => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ [name]: value }, () => {
      this.validateField(name, value);
    });
  };

  validateField(fieldName, value) {
    let fieldValidationErrors = this.state.formErrors;
    let emailValid = this.state.emailValid;

    switch (fieldName) {
      case "email":
        emailValid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
        fieldValidationErrors.email = emailValid ? "" : " is invalid";
        break;
      default:
        break;
    }
    this.setState(
      {
        formErrors: fieldValidationErrors,
        emailValid: emailValid,
      },
      this.validateForm
    );
  }

  validateForm() {
    this.setState({
      formValid: this.state.emailValid
    });
  }

  errorClass(error) {
    return error.length === 0 ? "" : "has-error";
  }

  storeWinnerEmail = () => {
    const { email } = this.state
    const storedEmails = JSON.parse(localStorage.getItem("emails"));
    storedEmails.push(email);
    localStorage.setItem('emails', JSON.stringify(storedEmails));
    debugger
  }

  render() {
    return (
      <form className="demoForm">
        <h2>Congratulations, you have won the quizz !</h2>
        <h3>Type your email in order to partifcipate to the draw and win an iPad </h3>
        <div
          className={`form-group ${this.errorClass(
            this.state.formErrors.email
          )}`}
        >
          <label htmlFor="email">Email address</label>
          <input
            type="email"
            required
            className="form-control"
            name="email"
            placeholder="Email"
            value={this.state.email}
            onChange={this.handleUserInput}
          />
        </div>
        <div className="panel panel-default">
          <FormErrors formErrors={this.state.formErrors} />
        </div>
        <button
          type="submit"
          className="btn btn-primary"
          disabled={!this.state.formValid}
          onClick={this.storeWinnerEmail}
        >
          Submit
        </button>
      </form>
    );
  }
}

export default Form;